@charset "UTF-8";
.button {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  font-weight: 600;
  font-size: 0.9rem;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  /* 
    브라우저에서 드래그나 더블 클릭을 사용한 텍스트 선택 등을 막으려는 방법
    user-select : auto | all | none | text;
    auto : Default 값으로 브라우저 허용시 텍스트 선택 가능
    all : 더블클릭이 아닌 클릭 만으로도 선택이 가능
    none : 텍스트 선택 안됨
    text : 텍스트 선택이 가능
    */
  display: inline-flex; }
  .button.default {
    background: #fa5252; }
    .button.default:hover {
      background: #ff6b6b; }
    .button.default:active {
      background: #fa5252; }
  .button.gray {
    background: #495057; }
    .button.gray:hover {
      background: #868e96; }
    .button.gray:active {
      background: #495057; }
  .button.outline {
    border: 2px solid white;
    border-radius: 2px; }
    .button.outline:hover {
      background: white;
      color: #228be6; }
    .button.outline:active {
      background: rgba(255, 255, 255, 0.85);
      border: 2px solid rgba(255, 255, 255, 0.85); }
  .button:hover {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.115); }
  .button.disabled {
    background: #ced4da;
    color: #868e96;
    cursor: default; }
    .button.disabled:hover, .button.disabled:active {
      box-shadow: none;
      background: #ced4da; }
  .button + .button {
    margin-left: 0.5rem; }
