@charset "UTF-8";
.post-body .paper {
  padding: 2rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  background: white;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.125), 0 10px 10px rgba(0, 0, 0, 0.11);
  -webkit-transform: translateY(-3rem);
          transform: translateY(-3rem);
  margin: 0 auto;
  min-height: 20rem;
  width: 1024px;
  /* calc 는 띄어쓰기를 해주어야 한다. */ }
  @media (max-width: 1023px) {
    .post-body .paper {
      width: 768px; } }
  @media (max-width: 767px) {
    .post-body .paper {
      width: calc(100% - 2rem); } }
